 }
 .quick-look-card{
  padding-bottom: 20px;
  width: 90%;
  margin: auto;
 }

 .quick-look-button{
    margin-top: 5px;
    margin-right: 740px;
    display: none;
    /* border-bottom: 2px solid  var(--dark);   */
 }
 /* .for-bottom{
    border-bottom: 2px solid  var(--dark);   
    margin-top: 20px;
 } */
 .quick-look-button a{
    margin-inline: 10px;
    border: none;
    padding: 6px;
    text-align: center;
    font-size: 17px;
    text-decoration: none;
    color:var(--dark);
    background-color:var(--orange);
     border: none;
 }
 .quick-look-button a:hover{
    background-color:var(--blue);
    color:#e0fbfc;
    border: none;
    
 }
 
 .App {
    position: relative;
}
.App button.slick-arrow.slick-next {
    top: 6%;
    border: none;
    background-color:var(--blue);
}
.App button.slick-arrow.slick-next:hover{
    background-color:var(--sky-blue);
    border: none;
}
.App button.slick-arrow.slick-prev {
    background-color:var(--blue);
    position: absolute;
    top: 6%;
    left: 88%;
    z-index: 1;
}
.App button.slick-arrow.slick-prev:hover{
    background-color:var(--sky-blue);

}


 

 
 
.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
    /* color: transparent; */
    outline: none;
    /* background: transparent; */
}
 
.slick-next:hover, .slick-next:focus, .slick-prev:hover, .slick-prev:focus {
    /* color: transparent; */
    outline: none;
    /* background: transparent; */
}



.view-offer-a{
    /* margin-left: 115px; */
    margin: 0px auto;
    text-decoration: none;
    background-color: white;
    color: var(--orange);
    border: 1px solid var(--orange);
    padding: 7px 20px;
    border-radius: 3px;
    text-align: center;
    transition: 0.3s;
    font-size: 12px;
    margin-top: 20px;
}   
.view-offer-a:hover{
    background-color:var(--orange);
    color: white;
}