.used-vehicle-ssection{
    
    margin: 0px auto;
    /* box-shadow: 0 1px 3px rgba(36,39,44,.15); */
    padding: 10px;
}

.used-vehicle-buuton{
    margin-bottom: 50px;
}
.used-vehicle-section .tanker {
    border: 1px solid #8585851a;
}
.used-vehicle-section button.slick-arrow.slick-prev:before {
    content: "<";
    font-size: 16px;
    font-family: monospace;
    background: transparent;
    color: white;
}
.used-vehicle-section button.slick-arrow.slick-next:before {
    content: ">";
    font-size: 16px;
    font-family: monospace;
    background: transparent;
}
.used-vehicle-section .section-title {
    padding: 23px 6px;
}
.usedVehicle-cards{
    gap: 1rem;
    margin: 0px 20px 10px 20px ;
    box-shadow: 0 1px 3px rgba(36,39,44,.15);
    justify-content: center;
    padding-bottom: 5px;
    text-align: center;
}
.usedVehicle-cards img{
    width: 80%;
   margin-inline: 10%;
   margin-top: 10px;
   height: 80px;
   filter: hue-rotate(400deg);

}
.usedVehicle-cards a{
    text-decoration: none;
}
:root {
    --blue: #3d5a80;
    --light-blue: #98c1d9;
    --sky-blue: #e0fbfc;
    --orange: #ee6c4d;
    --dark: #293241;
  }

.used-car-title{
    font-size: 11px;
    color:var(--blue) ;
    line-height: normal;
    text-align: center;
  
}
.used-car-title p{
    text-decoration: none;
   
}
.used-car-city{
    font-size: 15px;
    color:black ;

    text-align: center;
}

#used-vehicle-section
button.slick-arrow.slick-next {
    top: -10%;
    border: none;
    background-color:var(--blue);
}
 
#used-vehicle-section
button.slick-arrow.slick-prev {
    background-color:var(--blue);
    position: absolute;
    top: -10%;
    left: 88%;
    z-index: 1;
}