

   .enquiry-form {
    padding: 30px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    background-image: radial-gradient(circle at 50% 100%, #293957, #293957 20%, #ce8339 40%, #ce8339 60%, #293957 80%, #293957);  }
  
  .enquiry-form h2 {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 28px;
    font-weight: 500;
    color: #fff;
    text-align: center;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  }
  .Enquirybackground{
    background-image: url(./images/enquirynowpic.jpg);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-size: cover;
    filter: blur(2px);
  }
  .EnquiryAll {
    display: flex;
    justify-content: center;
    margin: 6%;
}
.Enquiryformouter {
    width: 40%;
}
.EnquiryContent {
    width: 40%;
    position: relative;
    display: flex;
    /* align-items: center; */
    justify-content: flex-start;
    /* margin-top: 10%; */
}
  
  .enquiry-form label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  }
  
  .enquiry-form input,
  .enquiry-form textarea {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    color: #555;
    transition: border-color 0.2s ease-in-out;
  }
  
  .enquiry-form input:focus,
  .enquiry-form textarea:focus {
    outline: none;
    border-color: #ce8339;
  }
  
  .enquiry-form textarea {
    resize: none;
    height: 120px;
  }
  
  .enquiry-form button[type="submit"] {
    background-color: #ce8339;
    color: #fff;
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .enquiry-form button[type="submit"]:hover {
    background-color: #ce8339;
  }
  
  .enquiry-form .form-error {
    display: block;
    margin-top: -20px;
    margin-bottom: 20px;
    font-size: 14px;
    color: #ce8339;
    font-weight: 500;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  }
  .EnquiryContent h3 {
    color: white;
    margin-top: 10%;
}
.EnquiryContent p {
    color: white;
}
.EnquiryContent{
    display: flex;
    flex-direction: column;
    text-align: center;
}
  @media only screen and (max-width: 600px) {
    .EnquiryAll {
        position: relative;
        
    }
    .EnquiryContent {
        position: absolute;
        width: 100%;
        height: 100%;
        
    }
    .Enquiryformouter {
        width: 80%;
        margin-top: 21%;
    }
    
    form.enquiry-form {
        z-index: 55;
        position: relative;
    }
    form.enquiry-form input {
        padding: 3px;
        font-size: 13px;
    }
    .enquiry-form label {
        font-size: 10px;
    }
    .EnquiryAll {
        margin: 0;
    }
    .EnquiryContent {
        display: block;
        text-align: center;
        
    }
  }
  