/* headers */
.header-title{
    margin-top: -50px;
    margin-left: 25px;
}
.title{
    font-size: 42px;
    font-weight: 800;
    color:white;
    text-transform: uppercase;
    
}
.title-para{
    color:white;
    margin-top: 20px;
    margin-left: 31px;
}

.Header-blog{
    display: flex;
    position: absolute;
    top: 70%;
}
.heaers-li{
    display: flex;
    gap: 30px;
    margin-bottom: 20px;
}
.heaers-li li{
    list-style: none;
    
}
li a{
    text-decoration: none;
    color:white;
}
li a:hover{
    color: #ce8339;
    transition:  .3s ease-out;
    text-decoration: underline;
}
.left-button-slide{
    margin-left: 10px;
    margin-top: 50px;
}
.right-button-slide{
    margin-left: 30px;
    margin-top: 50px;
}
 
.right-button-slide button{
    border:none;
    margin: 5px;
    padding: 5px;
    width: 30px;
    height: 30px;
}
.left-button-slide button{
    border:none;
    margin-inline: 5px;
    padding-inline: 5px;
    width: 30px;
    height: 30px;
}
