/* mid-section-of-blog */
.mid-full-page{
    display: flex;
    /* justify-content: space-around; */
}
.mid-section-blog{
    /* border: 1px solid red; */
    height: auto;
    width: 60%;
    display: flex;
    margin-top: 50px;
    flex-direction: column;
} 
.blog-list-container{
height: 90%;
margin-top: 50px;
display: flex;
margin-inline: 5%;
}
.card-blog{
    display: flex;
    flex-direction: column;
}
.blog-list-container  hr{
       margin-right: 5px;
       margin-top: 5px;
       margin-bottom: 0px;
}
.hr hr{
    margin-top: 20px;
}


.blog-img img{
    width: 270px;
    height: 155px;
}
.News{
    display: flex;
}
.News p{
    margin-left: 11px;
    margin-bottom: 6px;
}

.News a{
    font-size: 14px;
    font-weight: 700;
    color: #ce8339;
    text-decoration: none;
    margin-right: 10px;
}
.news-title h5 a{
    color:black;
    text-decoration: none;
}
.news-title h5 a:hover{
    color: #ce8339;
    transition:  .3s ease-out;
}
.news-title h5{
    font-size: 17px;
}
.news-title p{
    color: rgb(130, 130, 130);
    font-size: 13px;
    margin-bottom: 0px;
}
.blog-info{
    margin-left: 15px;
    box-sizing: border-box;
}

.blog-comment:hover{
    color: #ce8339;
    transition:  .3s ease-out;
}
.commnet-and-others{
    display: flex;
    justify-content: space-between;
}
.blog-share:hover{
    color: #ce8339;
    transition:  .3s ease-out;
}
.pagination-blog-list button{
    margin-inline: 5px;
    border: none;
}
.pagination-blog-list button:hover{
    background-color: #ce8339;
    transition:  .3s ease-out;
}
.pagination-blog-list{
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
}

/* right-sectionn */

.right-section{
    /* border: 1px solid rgb(255, 225, 0); */
    width: 35%;
height: 90%;
margin-top: 100px;
margin-left: 20px;
}
.top-section{
    margin-left: 33px;
}
.text-of-categouries{
    margin-top: 20px;
}

.list-number{
    float: right;   
}
.categouries-list  li a{ 
    color: rgb(21, 21, 20);
}
.categouries-list  li a:hover{
    color: #ce8339;
    transition:  .3s ease-out;
}
.categouries-list li{
    border-bottom: 1px solid rgb(183, 183, 183);
    list-style: none;
    margin-top: 20px;
}
/* populer-posts */

.populer-posts-heading{
    margin:50px 0px 30px 30px;
}
.populer-posts{
    margin-left: 33px;
    display: flex;
    border-bottom:solid rgb(183, 183, 183);
    margin-bottom: 20px;
}
.populer-post-img img{
    height: 55px;
    width: 90px;
}
.populer-head{
    margin-left: 10px;
}
.populer-details{
    margin-left: 10px;
    color:black;
    display: inline-block;
    margin-bottom: 10px;
}
.populer-details:hover{
    color: #ce8339;
    transition:  .3s ease-out;
}
.newSelter-full-box{
    height: 280px;
    width: 310px;
    margin-top: 80px;
    background-color: #f5f6f6;
    margin-bottom: 50px;
    margin-left: 30px;
}

/* NEWS-SELTER */

.selter-head{
    margin-top: 30px;
    display: inline-block;
}
.selter{
    margin-left: 25px;
}
.form input{
    width: 100%;
    padding: 0 15px;
    height: 45px;
    border-radius: 3px;
    border: 1px solid #d7dce6;
    background-color: #ffffff;
    font-size: 16px;
    /* color: #293957; */
}
.selter-form{
    margin-inline: 20px;
}
.btn-main{
    margin-top: 4%;
    padding: 1.9%;
    background-color:#ce8339;
    border:none;
    /* width: 21%; */
    color: white;
    letter-spacing: 1px;
    font-weight: 600;
    padding: 10px 10px;
    border-radius: 2px;
    padding-inline:15px;
}
.btn-main:hover{
    background-color: #204060;
    transition:  .5s ease-out;
}
.spam-text{
    /* margin-top: 20px; */
    margin-bottom: 20px;
}


.populer-button button{
    margin: 10px 10px 10px 10px ;
}

.populer-text{
    display: inline-block;
    margin-left: 30px;
}