#Newsletter {
    position: relative;
}
.newsletter-section{
      background-size: cover;
      height: 550px;
      /* position: relative; */
}
.Newsletter-full-page {
   
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 650px;
}
.Newslatter-background-img {
    background-image: url('../../public/sky-news.jpg');
    background-size: cover;
    background-position-y: -209px;
    position: absolute;
    width: 100%;
    height: 100%;
    filter: brightness(0.5);
    background-attachment: fixed;
}
.written-section-newsletter p {
    color: white;
}
.newsletter-input_button input {
    width: 53% !important;
}
.newsletter-section img{
    position: absolute;
    background:none;
    height: 400px;
    width: 40%;
    right: 0%;
    top: 20%;
}
.written-section-newsletter{
    top: 10%;
    /* border: 1px solid red; */
    left: 3%;

}
.written-section-newsletter h1{
    font-size: 70px;
    color:white;
}
.written-section-newsletter p{
    font-size: 20px;
}

.newsletter-input_button{
    display: flex;
    gap: 1rem;
    margin-top: 30px;
    align-items: center;
}
.newsletter-input_button input{
    width: 20%;
    height: 50px;
}
.newsletter-input_button a{
    width: 20%;
    background-color: #ff702d;
    margin: 7px;
    padding: 12px;
    text-align: center;
    color: white;
    text-decoration: none;
    /* border-radius: 5px;
    /* border-radius: 5px; */
}
