    font-weight: 700;
    color: #293957;
    text-transform: uppercase;
    font-size: 14px;
    letter-spacing: 0.025em;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    white-space: nowrap;
}

