.carfeature {
  width: 100%;
}
.img_any_question {
  width: 24%;
  display: block;
  margin: 0px auto;
  height: 32%;
  border-radius: 50%;
  object-fit: cover;
}
.have-any-question-col {
  display: flex;
  align-items: center;
  position: relative;
}
.doubt-call {
  color: #ce8339;
}
.have-any-question {
  color: #999;
}
.have-any-question-col {
    width: 105%;
    height: 100%;
}
.car-hoving-right {
    transition: 1s;
    transform: translate(-15px,0px);
}
.have-any-question-col:hover  .car-hoving-right {
    transform: translate(-40px,0px);
}
.car-hoving-left {
    transition: 1s;
    transform: translate(-35px,0px);
}
.have-any-question-col:hover .car-hoving-left {
    transform: translate(-10px,0px);
}
.overflow-car{
    overflow: hidden;
}
@media only screen and (max-width: 768px) { 

  .col-md-4.car-hoving-left {
    width: 30%;
}
.col-md-4.logo-anyquestion {
  width: 20%;
}
.col-md-4.car-hoving-right {
  width: 30%;
}
.have-any-question-col {
  justify-content: space-between;

}
.car-hoving-right {
  transition: 1s;
  transform: translate(44px,0px);
}
.have-any-question-col:hover .car-hoving-right {
  transform: translate(20px,0px);
}
.img_any_question {
  width: 91%;
}
.have-any-question {
  font-size: 10px;
}
.doubt-call {
  font-size: 10px;
}
}