/* Become A Dealer CSS */

.become-dealer-banner-area {
    background-image: url(.././becomeadealerbanner.jpg);
    height: 630px;
    position: relative;
    z-index: 5;
}
/* section.become-dealer-banner-area {
    background: red;
} */
.bg_cover {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
}
.align-items-center {
    align-items: center!important;
}
.become-dealer-banner-area::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: (linear, left top, right top, from(rgb(0, 0, 0)), to(rgba(255, 255, 255, 0)));
    background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgba(255, 255, 255, 0) 100%);
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.65;
}
@media (min-width: 992px){
.col-lg-5 {
    flex: 0 0 auto;
    width: 41.66666667%;
}}
@media (min-width: 576px){
.col-sm-9 {
    flex: 0 0 auto;
    width: 75%;
}}
.become-dealer-banner-content .main-title {
    font-size: 60px;
    font-weight: 800;
    text-transform: uppercase;
    color: #fff;
    line-height: 1;
    letter-spacing: -3px;
    text-shadow: 5px 5px 5px orange;
}

.become-a-dealer-content {
    padding-top: 112px;
    padding-bottom: 70px;
}

.dealer-title .title {
    font-size: 36px;
    font-weight: 700;
    font-family: "Cabin", sans-serif;
    color: #293957;
}
.single-benefits {
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin-top: 45px;
}
.single-benefits .benefits-icon i {
    width: 80px;
    font-size: 50px;
    color: #ce8339;
    line-height: 45px;
}
.ion-model-s:before {
    content: "\f2c1";
}
.single-benefits .benefits-content {
    -moz-flex: 1;
    -ms-flex: 1;
    -webkit-box-flex: 1;
    flex: 1;
}
.single-benefits .benefits-content p {
    font-size: 20px;
    font-weight: 700;
    color: #293957;
}
.ion-android-contacts:before {
    content: "\f2d9";
}
.ion-cash:before {
    content: "\f316";
}
.benefits-btn {
    margin-top: 65px;
}
.benefits-btn .main-btn {
    font-size: 14px;
}

.benefits-btn :hover{
    color: #ce8339;
    background-color: #fff;
}
.call-to-action-2-btn :hover{
    color: #ce8339;
    background-color: #fff;
}
.main-btn {
    display: inline-block;
    font-weight: 700;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 0;
    padding: 0 20px;
    font-size: 16px;
    height: 45px;
    line-height: 45px;
    border-radius: 2px;
    color: #fff;
    cursor: pointer;
    z-index: 5;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    text-transform: uppercase;
    background-color: #ce8339;
    letter-spacing: 1px;
}

p {
