footer.footer-outer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
div#footer {
    padding: 83px 0;
}
div#footer {
    padding: 83px 0;
    background-color: var(--blue);
    padding-bottom: 32px;
    z-index: 999;
    position: relative;
}
div#footer p {
    color: #99a3b6;
    margin-top: 41px;
}
.footer-content {
    width: 20%;
    display: flex;
    justify-content: space-around;
    border-right: 1px solid #ffffff;
}
.footer-heading h3 {
    color: white;
}
.footer-content ul {
    padding: 0;
    list-style: none;
    margin-top: 41px;
}
.footer-content a {
    text-decoration: none;
    color: #99a3b6;
}
.footer-heading {
    margin-top: 56px;
}
.footer-content input {
    background: #020635;
    border: 1px solid #232369;
    padding: 3px 20px;
    color: white;
}
.footer-content button {
    background: #035203;
    padding: 4px 24px;
    margin-top: 13px;
    border-radius: 7px;
    color: white;
}
.footer-logo img {
    width: 100%;
}
.footer-bottom {
    display: flex;
    align-items: center;
}
.footer-copyright {
    width: 100%;
    text-align: center;
}
.footer-copyright a {
    color: #99a3b6;
}
.footer-bottom {
    display: flex;
    align-items: center;
    border-top: 1px solid white;
    margin-top: 28px;
}
.footer-copyright p {
    margin-top: 16px !important;
}
div#footer .nabar-social-media a {
    color: #8a8ca7;
}
.brand-info p {
    margin-top: 0 !important;
}
.foater-height {
    height: 91px;
}