.logo-car {
  color: #000;
  text-align: center;
}
.electric-vehicle-log-outer {
    display: flex;
    justify-self: center;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}
.electric-car-brand-img {
    width: 93px;
    height: 70px;
}
/* .electric-vehicle-log-outer.m-4 a {
    width: 12%;
    text-align: center;
} */

.electric-vehicle-log-outer.m-4 a {
    width: 12%;
    text-align: center;
    box-shadow: 1px 4px 7px -2px black;
    margin-left: 1%;
    margin-bottom: 2%;
    border-radius: 1px;
    padding: 10px;
    box-shadow: 0 1px 3px rgba(36,39,44,.15);
}
.electric-vehicle-log-outer.m-4 p {
    display: none;
}
.electric-vehicle-logo-name {
    display: flex;
    justify-content: space-between;
}
.browserBY {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.electric-vehicle-border {
    border-bottom: 1px solid #2932410d;
}
.show-all-makes {
    color: #999;
    text-decoration: revert;
}
.car-brand-clr{
    color: #008000a8
}
#electric-vehicle-log{
    margin-top: 2%;
}
.browserBY {
    display: flex;
    justify-content: space-between;
    width: 49%;
    align-items: center;
}
