
.admin-bg-div {
    background-image: url(images/admin-bg.jpg);
    position: fixed;
    height: 100%;
    width: 100%;
    background-size: cover;
    z-index: -1;
}
div#admin-page-id .tanker {
    position: fixed;
    top: 41%;
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: center;
    left: 0;
    right: 0;
}
div#admin-page-id form {
    display: flex;
    flex-direction: column;
    width: 43%;
    background: #24242429;
    padding: 13px;
    border-radius: 10px;
    box-shadow: 10px 10px 31px #0c0d09;
}
div#admin-page-id input {
    margin: 10px 53px;
    border: none;
    background: transparent;
    border-bottom: 1px solid #12ff17a8;
    margin-bottom: 64px;
}
div#admin-page-id input::placeholder {
    color: black;
}
div#admin-page-id button {
    background: #ce8339;
    border: navajowhite;
    width: 39%;
    padding: 10px;
    border-radius: 10px;
}
