.dealerlist {
    padding: 100px 10px 58px 95px;
}
.find-dealer-block {
    background-color: #f5f6f6;
}
.find-dealer {
    font-weight: 700;
    font-size: 19px;
}
.select-car{
    width: 300px;
    border: none;
}
.bg-select{
    background-color: #fff;
    margin: 0px 8px;
}
.search-btn {
    padding: 12px 75px;
    border-radius: 0px;
    font-size: 20px;
    background-color: #ce8339;
    border: none;
}
.location-padding {
    padding: 26px 0px;
}
.reset-btn {
    display: flex;
    justify-content: flex-end;
}
.flatNo{
    color: #ce8339;
}
.dealer-address {
    font-weight: 600;
    font-size: 18px;
}
select.sorting {
    width: 328px;
    float: right;
    background-color: #edeff1;
    border: none;
    padding: 10px 10px;
    font-weight: 600;
}
.car-logo {
    width: 100px;
    border-radius: 50%;
    height: 100px;
    object-fit: cover;
}
.travelCar {
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: 600;
}
.rating-btn{
    list-style: none;
}
.rating-star{
    padding: 0px;
    display: flex;
}
.rating-on {
    color: gold;
    padding: 1px;
}
.items-name {
    font-weight: 700;
}
.address-deal {
    color: #0084c8;
    font-weight: 700;
}
.google-find{
    font-weight: 400;
    color: #737f96;
    font-size: 14px;
    text-decoration: underline;
}
.car-purchase{
    font-size: 16px;
    font-weight: 400;
    color: #737f96;
}
.purchase-no{
    font-weight: 700;
}
.dealer-location {
    width: 16px;
    height: 16px;
}
.fa.fa-rotate-left {
    padding-top: 6px;
}
.row.flex-itencentr {
    align-items: center;
}
.dealerlist {
    padding: 29px 48px;
}
.select-car:focus-visible {
    outline: none;
  }